.stickyContainer{
    width: 100%;
    position: fixed;
    padding: 0 3% 0 3%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-bottom: 1px solid lightgray;
    background-color: #fff;
}

.stickyComponents{
    display: inline-block;
    margin: 10px 30px 5px 30px;
}

select{
    background-color: #fff;
    padding: 10px;
    width: 140%;
    border: 1px solid #ddd;
    border-radius: 3%;
}

select option{
    left:50%;
}

input{
    vertical-align: middle;
    padding: 10px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 4%;
}

label{
    position: relative;
}



