.tabWrapper{
    position: absolute;
    top:0%;
    left:0%;
    width: 100%;
}

.tabContainer{
    top:8%;
    left:0px;
    width: 100%;
    height: 8.3%;
    border-bottom: 1px solid lightgray;
    text-align: center;
}

.tabs{
    display: inline-block;
    position: relative;
    margin:25px 30px 3px 30px;
    padding: 0 0 0.4em 0;
    font-size: 1em;
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
}

.tabs:hover{
    color: aqua;
}

.contentContainer{
    position: absolute;
    width: 100%;
    top:5%;
}

.content{
    position: absolute;
    -webkit-transition: all 0.6s;
    -o-transition: all 0.6s;
    transition: all 0.6s;
}


