.lightBoxContainer{
    position: absolute;
    width: 100%;
}

.lbContainer{
    position: absolute;
    background-color: red;
    left:2%;
    width: 96%;
    height: 100%;
    background-color: #fff;
    overflow: hidden;

}

.leftButton{
    position: absolute;
    width: 3%;
    height: 100%;
    left: 2%;
    background-color:transparent;
}

.rightButton{
    position: absolute;
    right:2%;
    width: 3%;
    height: 100%;
    background-color:transparent;
}

.leftButton:hover,.rightButton:hover{
    background-color:rgba(128,128,128,0.5);
    -webkit-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
}

.leftButton > i, .rightButton>i {
    position: absolute;
    text-align: center;
    top: 38%;
    left: 30%;
}

button i{
    text-align: center;
    vertical-align: middle;
}

img{
    width: 100%;
    height: 100%;
    padding: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.closeButton:hover{
    -webkit-transform: rotate(405deg);
        -ms-transform: rotate(405deg);
            transform: rotate(405deg)
}

.leftLightButton > i, .rightLightButton>i {
    position: absolute;
    text-align: center;
    top: 25%;
    left: 30%;
}

.leftLightButton:hover,.rightLightButton:hover{
    background-color:rgba(255,128,128,0.5);
    -webkit-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
}


